.greeting-container {
  background-color: palegreen;
  position: relative;
  height: 100vh;
}

.greeting-container h2 {
  margin: 0;
}

.greeting-card {
  position: absolute;
  background-color: skyblue;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 80%;
  height: 80%;
}



